import React from 'react';
import ReactDOM from 'react-dom';
import './custom.css';

// REACT-ROUTER
import { HashRouter, Route, Link  } from 'react-router-dom';

// MAIN PAGE
import MainPage from './mainPage';

// MOVIE DETAILS PAGE
import MovieDetail from './movieDetail';


// HEADER
import Header from './components/Header';

// APP CALL | CREATE PAGE
function DokaMovie() {
    return (
        <div className="DokaMovies">
            <Header />
            <Route exact path={process.env.PUBLIC_URL + '/'} component={MainPage} />
            <Route exact path="https://cihannos.github.io/doka-case-study/moviedetail" component={MovieDetail} />
        </div>
    );
}


// RENDER
ReactDOM.render(
    <HashRouter><DokaMovie /></HashRouter>,
    document.getElementById("root")
);